import React from 'react'
import { NavLink } from 'react-router-dom'

export default function Nav() {
    return (
        <div className='nav'>
            <ul>
                <li><NavLink to='/twitter-clone/' activeClassName='active'>Home</NavLink></li>
                <li><NavLink to='/twitter-clone/new' activeClassName='active'>New Tweet</NavLink></li>
            </ul>
        </div>
    )
}
